// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from "element-ui"
import echarts from 'echarts'
import VueRouter from 'vue-router'

Vue.config.productionTip = false

Vue.prototype.$echarts = echarts
// Vue.prototype.baseUrl = "http://192.168.31.162"   //开发
Vue.prototype.baseUrl = "http://zg.demo.xqit.net"  //线上

import { 
  Carousel
} from 'element-ui'

Vue.use(ElementUI)
Vue.use(Carousel)
Vue.use(VueRouter)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

router.beforeEach((to, from, next) => {
  let getLogin = window.sessionStorage.getItem('login')

  if(getLogin == 'isLogin'){
    next()
  }else{
    if(to.matched[0].meta.isLogin){
      // window.location.href = 'http://zg.demo.xqit.net:80/#/login'    //开发
       window.location.href = 'http://zg.demo.xqit.net/ydjxzg/index.html#/login'    //线上
    }else{
      next()
    }
  }
})


