import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: resolve=>require(["@/components/HelloWorld.vue"], resolve),
    },
    {
      path: '/index',
      name: 'index',
      component: resolve=>require(["@/components/index.vue"], resolve),
      children:[
        {
          path: '/',
          name: 'jigou',
          component: resolve=>require(["@/components/jigou.vue"], resolve),
        },
        {
          path: '/renyuan',
          name: 'renyuan',
          component: resolve=>require(["@/components/renyuan.vue"], resolve),
        },
        {
          path: '/quanxian',
          name: 'quanxian',
          component: resolve=>require(["@/components/quanxian.vue"], resolve),
        },
        {
          path: '/canshu',
          name: 'canshu',
          component: resolve=>require(["@/components/canshu.vue"], resolve),
        }
      ]
    }
  ]
})



